@import url(https://fonts.googleapis.com/css?family=Comfortaa|Syncopate:700);
@import url(https://fonts.googleapis.com/css?family=Prata);
.header {
  padding: 1rem;
  margin: 0;
  color: #f5c114;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  height: 4rem;
  width: 100%;
  background: #070713;
  z-index: 50; }

.desktop-nav {
  width: 90%;
  display: flex;
  justify-content: flex-end; }

.nav {
  text-transform: uppercase;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  color: inherit;
  text-decoration: none;
  font-size: .9rem;
  padding: 1.4rem 1rem; }
  .nav:hover {
    text-shadow: #070713 1px 3px 2px;
    font-weight: bold;
    background: #04619f; }

.user-greeting {
  font-weight: bold;
  font-size: 1.2rem;
  font-family: "Syncopate", sans-serif;
  margin-right: 3rem; }

.logo-wrapper {
  display: grid;
  grid-template-rows: 1rem 2.5rem;
  background: rgba(100, 98, 98, 0.536);
  padding: 0 3px 8px 3px;
  cursor: pointer;
  position: relative; }

.icon-text {
  font-family: "Syncopate", sans-serif;
  font-size: 1.5rem;
  margin: 0 0 5px 0;
  padding: 5px 1px 3px 1px; }

.film-icon {
  position: absolute;
  bottom: -3px;
  right: 13px;
  width: 2rem;
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg); }

.menu-icon {
  display: none; }

@media screen and (min-width: 320px) and (max-width: 750px) {
  body {
    font-size: 14px; }
  .desktop-nav {
    display: none; }
  .menu-icon {
    display: block;
    height: 2rem;
    width: 2rem;
    color: #e8e3e3;
    cursor: pointer; }
    .menu-icon:hover {
      color: #f5c114; } }

.movies-section {
  width: 94%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  margin: 3%; }
  .movies-section h2 {
    text-align: center;
    color: #f4c112; }

@media screen and (min-width: 320px) and (max-width: 750px) {
  .movies-section {
    grid-template-columns: repeat(2, 1fr); } }

.movie-page-container {
  width: 100%;
  display: grid;
  grid-template-areas: "banner banner" "poster text" ". text"; }

.movie-banner-wrapper {
  grid-area: banner;
  width: 100%;
  margin-top: 5rem; }

.movie-banner-img {
  width: 100%; }

.poster-container {
  grid-area: poster;
  margin: 2rem 0 10rem 4rem; }

.movie-poster-img {
  width: 90%; }

.movie-text-container {
  grid-area: text;
  width: 100%;
  background: rgba(255, 255, 255, 0.482);
  padding: 3rem;
  margin-top: -10px;
  z-index: -1; }

.overview-text {
  text-align: justify; }

.login-page {
  padding: 5rem 0 0 0;
  height: 120vh;
  text-align: center; }

.form-heading {
  color: #f5c114;
  font-family: "Syncopate", sans-serif;
  text-shadow: #d3a012 1px 3px 2px, #474747 3px 5px 3px, 2px 2px 2px rgba(210, 160, 18, 0);
  font-size: 3rem; }

.login-form {
  margin: 0 auto; }

.login-fieldset {
  width: 60%;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  margin: 0 auto;
  border: 1px solid #d3a012;
  padding: 2rem;
  background: rgba(6, 6, 19, 0.39);
  box-shadow: inset 0px 0px 15px rgba(100, 98, 98, 0.536);
  -moz-box-shadow: inset 0px 0px 15px rgba(100, 98, 98, 0.536);
  -webkit-box-shadow: inset 0px 0px 15px rgba(100, 98, 98, 0.536); }

label {
  color: #e8e3e3;
  font-weight: bold;
  display: block;
  width: 100%;
  padding: 1rem;
  text-transform: uppercase; }

input {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 4px;
  font-size: 1.1rem;
  margin: 0;
  outline: #070713;
  padding: 1rem;
  width: 70%;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
  margin-bottom: 30px;
  text-align: center; }

#form-submit-btn {
  position: relative;
  display: block;
  padding: 2rem auto;
  color: #e8e3e3;
  margin: 0 auto;
  background: rgba(4, 97, 159, 0.407);
  font-size: 1.1rem;
  text-align: center;
  width: 70%;
  border: 1px solid #070713;
  border-width: 1px 1px 3px;
  margin-bottom: 10px;
  cursor: pointer; }
  #form-submit-btn:hover {
    background: #070713;
    color: #e8e3e3; }

.login-error {
  color: red;
  cursor: default;
  font-size: 1.2rem; }

.signup-link {
  cursor: pointer; }

.banner-section {
  position: relative; }

.movie-tiles {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(32, 1fr); }

.tile {
  position: relative;
  -webkit-filter: brightness(70%);
          filter: brightness(70%);
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: -1; }

.banner-heading {
  position: absolute;
  z-index: 1;
  bottom: 10%;
  right: 10%;
  margin: 0;
  font-size: 4rem;
  color: #f5c114;
  font-family: "Syncopate", sans-serif;
  text-shadow: #d3a012 1px 3px 2px, #474747 3px 5px 3px, 2px 2px 2px rgba(210, 160, 18, 0); }

@media screen and (max-width: 750px) {
  .banner-heading {
    right: 5px; }
  .banner-heading {
    font-size: 2rem; } }

.movie-card {
  display: inline-block;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  border-radius: 3px;
  background: #070713;
  box-shadow: 3px 2px 2px #284b62;
  position: relative;
  color: #e8e3e3;
  overflow: hidden;
  cursor: pointer;
  text-align: center; }
  .movie-card:hover {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
    box-shadow: 5px 5px 5px #284b62; }

.poster-img {
  border-radius: 3px;
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 0; }

.card-hover-heading {
  width: 100%;
  grid-area: title;
  font-family: "Prata", serif;
  background: rgba(100, 98, 98, 0.536);
  padding: 1rem 0;
  font-size: 2rem;
  align-self: center;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.61); }

.card-hover {
  opacity: 0;
  background: rgba(9, 7, 27, 0.671);
  display: grid;
  grid-template-rows: 8fr 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "title title" "fav  info" "prompt prompt";
  position: absolute;
  justify-content: center;
  top: 0;
  height: 100%;
  width: 100%; }
  .card-hover:hover {
    opacity: 1;
    z-index: 5; }

.favorite-btn {
  grid-area: fav;
  cursor: pointer; }

.prompt {
  color: #f5c114;
  grid-area: prompt;
  font-size: .8rem;
  margin: .5rem; }

.prompt-placeholder {
  color: transparent; }

.more-info-btn {
  grid-area: info;
  align-self: center; }

.star {
  height: 1.8rem;
  padding: 0 0 5px 0; }

.btn {
  margin: 0 auto;
  padding: .5rem 0;
  width: 60%;
  border-radius: 50px;
  background: rgba(255, 255, 255, 0.482);
  outline: transparent;
  border: 2px solid transparent;
  cursor: pointer; }
  .btn:hover {
    border: 2px solid #f5c114;
    background: #e7e4e4; }

* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "Comfortaa", cursive;
  background-color: #000000;
  background-image: -webkit-linear-gradient(303deg, #000000 2%, #04619f 95%);
  background-image: linear-gradient(147deg, #000000 2%, #04619f 95%); }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

